body{
  background-color: #ededed;
}
nav{
  display: flex;
  justify-content: space-around;
  align-items: center;
}
nav .box{
  width: 60px;
  height: 60px;
  background-color: gray;
}
.navbar-nav{
  display: flex;
}
.nav-item{
  margin: 25px;
  list-style: none;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.nav-link{
  text-decoration: none;
  color: #2b2b2b;
}
.download-btn button{
  background-color: #423f3e;
  color: white;
  padding: 15px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-weight: 700;
}
.texts{
  text-align: center;
  margin: 150px auto;
}
.texts h3{
  font-size: 35px;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;

}
.texts p{
  padding: 0 100px;
}
.placeholder{
  margin-top: 250px;
  display: flex;
  align-items: center;
  padding: 0 110px;
}
.placeholder-title{
  font-size: 35px;
  color: #2b2b2b;
}
.sections{
  display: flex;
  width: 75%;
}
.link{
  display: flex;
  align-items: center;
  font-size: 17px;
  color: #2b2b2b;
  text-decoration: none;
  font-weight: 700;
}
.link .rightIcon{
  font-size: 35px;
}
.right-side img{
  border-radius: 20px;
}
.services{
  display: flex;
  align-items: baseline;
  padding: 15px 110px;
}
.services-left{
  flex: 1;
}
.services-left h2{
  font-size: 35px;
}
.services-left p{
  width: 60%;
}
.services-main{
  display: flex;
}
.services-main ul li{
  list-style: none;
  line-height: 50px;
  font-size: 25px;
  font-weight: 500;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}
.container{
  display: flex;
  padding: 80px 90px;
  justify-content: space-around;
  align-items: center;
}
.design{
  font-size: 30px;
}
.container p{
  width: 60%;
}
.container img{
  width: 200px;
  height: 200px;
  display: flex;
  justify-content: space-between;
  border-radius: 10px;
}
.cards-side{
  padding: 0 110px 40px;
}
.cards-title{
  text-align: center;
  font-size: 35px;
}
.cards{
  display: flex;
  justify-content: space-evenly;
}
.card{
  width: 400px;
  height: 250px;
  background-color: #EBEBEB;
  padding: 40px 15px 0;
  box-shadow: rgba(0,0,0,0.15) 1.5px 1.95px 2.6px;
  border-radius: 7px;
}
.card p{
  font-size: 18px;
  font-weight: 600;
}
.author p{
  margin-bottom: 0;
}